/**
 * 
 */
import Panel from '../panel'
export default class{
    constructor(editor){
        this.editor = editor
        this.$elem = $(
            `<div class="menu" title="">
                <i class="ico-rich ico-rich-emoticon"></i>
            </div>`
        )
        this.type = 'click'
        
        // 
        this.panelTemp = `
        <div class="editor-panel editor-panel-emoticon" >
        <span class="pointer"></span>
        ${this.getEmotTemp()}  
        </div>`
    }
    onClick(){
        const editor = this.editor
        //
        if(!this.editor.isFocus) return
        if(this.panel){
            if(this.panel.isShow){
                this.panel.hide()
            }else{
                this.panel.show()
            }
        }else{
            this.panel = new Panel({
                editor: this.editor, 
                elem: this.$elem, 
                menuKey: 'emoticon',
                temp: this.panelTemp
            })
            this.panel.show()
            const $panel = this.panel.$elem
            $panel.find('.emot-grid dd').on('click', e => {
                const emot = e.target.innerText
                editor.cmd.insertHTML(emot)
                this.panel.hide()
            })
        }
        
    }
    //
    changeActive(){
        if(this.panel && this.panel.isShow){
            this.$elem.addClass("active")
        }else{
            this.$elem.removeClass("active")
        }
    }
    // 
    getEmotTemp(){
        const emotDatas =  ['( _)', '()', '(`)', '()', '(   - )', '( )', '(  `)', '(`)', '((#)', '()', '()', ' (//)', '( )', '(;)', '_', '( ^^)', '()', '', '( `', '', '()', '_', '', '() ', '_', '_', '(==) ', '()', '/', ' _(:3 )_']
        let temp = '<dl class="emot-grid">'
        emotDatas.forEach((v, i) => {
            temp += `<dd>${v}</dd>`
        })
        temp += '</dl>'
        return temp
    }
}


